import React, { Component } from 'react';
import axios from 'axios';
import FileSaver from 'file-saver';
import JSZip from 'jszip' 
import './App.css';
import Grid from '@material-ui/core/Grid';
import Checkbox from '@material-ui/core/Checkbox';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import styled from 'styled-components';
import TextField from '@material-ui/core/TextField';
import { spacing, palette } from '@material-ui/system';
import LinearProgress from '@material-ui/core/LinearProgress';
const Box = styled.div`${spacing}${palette}`;

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isSigin: false,
      files: [],
      childrens: [],
      series: {},
      searchMethod: 'input',
      inputText: '',
      downloadType: {
        product: true,
        upc: false
      },
      downloadDone: false,
      searchItem: {},
      downloadLoading: false,
      percen: 0,
    }

    this.loadClient = this.loadClient.bind(this);
    this.loadFiles = this.loadFiles.bind(this);
    this.loadSeries = this.loadSeries.bind(this)
    this.seriesInit = this.seriesInit.bind(this);
    this.onDownloadZIP = this.onDownloadZIP.bind(this);
    this.downloadData = this.downloadData.bind(this);
    this.onSaveFileZIP = this.onSaveFileZIP.bind(this);
  }
  componentDidMount() {
    const { gapi } = window;
    gapi.load('client:auth2', this.initClient.bind(this));
  }

  initClient() {
    const { gapi } = window;

    gapi.client.init({
      apiKey: 'AIzaSyAAKJOAw6uBXpKhpAkXQy0e0pPFxpNMraM',
      clientId: '1090032071436-ejoeke6m3hupc8i8p41u6tf2pl1scmld.apps.googleusercontent.com',
      // discoveryDocs: "https://content.googleapis.com/discovery/v1/apis/drive/v2/rest",
      scope: 'https://www.googleapis.com/auth/drive.metadata.readonly https://www.googleapis.com/auth/drive.photos.readonly https://www.googleapis.com/auth/drive.readonly'
    }).then( () => {
      // Listen for sign-in state changes.
      // gapi.auth2.getAuthInstance().isSignedIn.listen(this.updateSigninStatus);
      // this.updateSigninStatus(gapi.auth2.getAuthInstance().isSignedIn.get());
    }, (error) => {
      console.log(error)
    }); 
  }
  updateSigninStatus (isSignin) {
    console.log(isSignin)
  }
  authenticate() {
    const { gapi } = window;
    return gapi.auth2.getAuthInstance()
      .signIn({scope: "https://www.googleapis.com/auth/drive.metadata.readonly https://www.googleapis.com/auth/drive.photos.readonly https://www.googleapis.com/auth/drive.readonly"})
      .then(function() { console.log("Sign-in successful"); },
            function(err) { console.error("Error signing in", err); });
  }
  loadClient() {
    const { gapi } = window;
    return gapi.client.load("https://content.googleapis.com/discovery/v1/apis/drive/v2/rest")
      .then(() => { 
        console.log("GAPI client loaded for API"); 
        this.setState({isSigin: true});
        /* gapi.client.drive.files.list({
          "q": "title='OVVO Product / UPC Finder'"
        })
          .then((res) => {
            this.loadSeries(res.result.items[0].id);
          },
          (err) => { console.error("Execute error", err);}) */
        
      },
      (err) => { console.error("Error loading GAPI client for API", err); });
  }
  handleAuthClick(event) {
    this.authenticate()
    .then(this.loadClient)
  }
  loadFiles() {
    const { gapi } = window;
    
    gapi.client.drive.files.list({})
      .then(
        (response) => {
          this.setState({files: [...response.result.items]})
        },
        (err) => { console.error("Execute error", err);}
      );
  }
  loadSeries (id) {
    const { gapi } = window;
    //project
    gapi.client.drive.files.list({
      "q": `'${id}' in parents`
    })
    .then((response) => {
      response.result.items.map(item => {
        gapi.client.drive.files.list({
          "q": `'${item.id}' in parents`
        })
        .then((res) => {
          if (res.result.items.length) {
            res.result.type = item.title;
            this.state.childrens = [...this.state.childrens, res.result];
            this.seriesInit()
          }
        }, (err) => { console.error("Execute error", err); })
      })
    },
    (err) => { console.error("Execute error", err); })
  }

  seriesInit() {
    const { childrens } = this.state;

    if (childrens.length >=2) {
      childrens.map((item) =>{
        item.items.map((i) =>{
          this.state.series[i.title] = this.state.series[i.title] || {};
          this.state.series[i.title][(item.type === 'UPC' ? 'upc' : 'product')] = i.id;
        })
      })
    }
  }
  onDownloadZIP() {
    const { gapi } = window;
    const { searchMethod, downloadType } = this.state;
    this.setState({downloadLoading: true, downloadDone: false, percen: 0, hint: ''});
    this.state.searchItem = {};
    if (searchMethod == 'input') {
      let data = this.state.inputText.split(/[\n||,]/);
      let searchQuery = data.map((item) =>{
        let ary = item.replace(/([A-Z]+)|([^A-Z]+)/ig, '-$1-$2').split(/[-_\/]/).filter((i) =>{ return i !='' && i != ' '})
        return ary;
      })

      searchQuery.map((item, index) => {
        if (item[0]) {
          let key = item.join('-');
          this.state.searchItem[key] = {
            data: []
          };
          let q = `fullText contains '${item[0]}'`;
          item.map((s, i) =>{
            if (i > 0) {
              q += ` and fullText contains '${s}'`;
            }
          });
          
          setTimeout(() =>{

          
          gapi.client.drive.files.list({
            q
          })
            .then((res) => {
              this.state.searchItem[key].items = res.result.items.filter((qItem) =>{
                if(downloadType.product && downloadType.upc && ( qItem.fileExtension === 'pdf' ||  qItem.fileExtension === 'jpg' ||  qItem.fileExtension === 'jpeg' ||  qItem.fileExtension === 'png')) {
                  return qItem;
                } else if(downloadType.product && qItem.fileExtension && qItem.fileExtension !== 'pdf'){
                  return qItem.fileExtension !== 'pdf';
                } else if(downloadType.upc && qItem.fileExtension && qItem.fileExtension == 'pdf'){
                  return qItem.fileExtension === 'pdf';
                } else {
                  return false;
                }
              })
              this.state.searchItem[key].items.map((qItem) =>{
                this.downloadData({key,url: qItem.downloadUrl,type: qItem.fileExtension, title: qItem.title })
              })
              

              if (!res.result.items.length || !this.state.searchItem[key].items.length) {
                this.state.searchItem[key].done = true;
                this.state.searchItem[key].status = 'fail';
                this.onSaveFileZIP();
              }
              
            },(err) => { 
              console.error("Execute error", err);
            })
          },150 * index)  
        }
      });
    } else {
      console.log(this.state.searchItem)
    }
  }

  downloadData({key, url, type, title}) {
    const { gapi } = window;

    axios({
      method:'get',
      responseType: 'arraybuffer',
      url,
      headers: {
        Authorization: `${gapi.auth.getToken().token_type} ${gapi.auth.getToken().access_token}`
      }
    })
    .then((res) => {
      let data;
      if (type = 'pdf') {
        data = new Blob([res.data], {type: 'application/pdf',})
      } else {
        data = new Buffer(res.data, "binary").toString("base64");
      }

      this.state.searchItem[key].data.push({data, type, key, title});
      
      if (this.state.searchItem[key].items.length === this.state.searchItem[key].data.length) {
        this.state.searchItem[key].done = true;
        this.onSaveFileZIP();
      }
    })
  }

  onSaveFileZIP() {
    const { searchItem } = this.state;
    let items = Object.keys(searchItem).map((item) => item);

    let doneItems = Object.keys(searchItem).filter((key) =>{
      return searchItem[key].done;
    })
    this.setState({percen: Math.ceil(doneItems.length / items.length * 100)})
    console.log(items.length , doneItems.length)
    if(items.length === doneItems.length) {
      console.log('start down zip')
      this.setState({hint: 'ZIP Compressing'})
      const zip = new JSZip();

      Object.keys(searchItem).map((key) =>{
        searchItem[key].data.map((item) =>{
          const folder = zip.folder(item.key);
          if (item.type === 'pdf') {
            folder.file(item.title, item.data, { binary: true });
          } else {
            folder.file(item.title, item.data, {base64: true});
          }
        })
      })
      
      if (Object.keys(zip.files).length) {
        zip.generateAsync({type:"blob"})
        .then((content) => {
            // see FileSaver.js
            FileSaver.saveAs(content, "OVVO_Product.zip");
            this.setState({downloadLoading: false, downloadDone: true});
        });
      } else {
        alert('Nothing download');
        this.setState({downloadLoading: false, downloadDone: true});
      }
    }
  }
  onSearchMethod(e) {
    this.setState({searchMethod: e.target.value})
  }
  onInputChange(e) {
    this.setState({inputText: e.target.value})
  }
  onSeriesChecked(key, e) {
    let series = this.state.series;
    series[key].checked = e.target.checked;
    this.setState({series: {...series}})
  }
  onDownloadType(key, e) {
    let downloadType = this.state.downloadType;
    downloadType[key] = e.target.checked;
    this.setState({downloadType: {...downloadType}})
  }
  render() {
    return (
      <div className="App root">
        <AppBar position="static" color="default">
          <Toolbar>
            <Typography variant="h6" color="inherit">
              OVVO Product / UPC Finder
            </Typography>
          </Toolbar>
        </AppBar>
        {
          !this.state.isSigin &&
          <Box p={2}>
            <Card>
              <CardContent>
                Log in to Google Drive
              </CardContent>
              <CardActions>
                <Button variant="contained" color="primary" onClick={this.handleAuthClick.bind(this)}>Log in</Button>
              </CardActions>
            </Card>
          </Box>
        }
        {
          this.state.isSigin &&
          <Box p={2}>
          {
            this.state.downloadLoading &&
            <div>
              <br />
              <LinearProgress variant="query" />
              <br />
              {`${this.state.percen}% ${this.state.hint}`}
            </div>
          }
          <Card>
            <CardContent>
              <Grid container spacing={24}>
                 {/*<Grid item xs={12}>
                  <FormControl component="fieldset">
                    <FormLabel component="legend">Search Method</FormLabel>
                    <RadioGroup
                      row
                      aria-label="searchMethod"
                      name="searchMethod"
                      value={this.state.searchMethod}
                      onChange={this.onSearchMethod.bind(this)}
                    >
                      <FormControlLabel
                        value="input"
                        control={<Radio />}
                        label="By product model"
                      />
                      <FormControlLabel value="series" control={<Radio />} label="By collection" />
                    </RadioGroup>
                  </FormControl>
                </Grid>*/}
                <Grid item xs={12}>
                  <TextField
                    id="outlined-multiline-flexible"
                    label="Model name / model color"
                    multiline
                    rowsMax="10"
                    value={this.state.inputText}
                    onChange={this.onInputChange.bind(this)}
                    margin="normal"
                    variant="outlined"
                    style={{width: '100%'}}
                  />
                </Grid>
                {/* {
                  this.state.searchMethod == 'series' &&
                  <Grid item xs={12}>
                    {
                      Object.keys(this.state.series).map((key,index) =>{
                        return <FormControlLabel  key={index}
                            control={
                              <Checkbox
                                checked={this.state.series.checked}
                                value={key}
                                color="primary"
                                onChange={this.onSeriesChecked.bind(this, key)}
                              />
                            }
                            label={key}
                          />
                      })
                    }
                  </Grid>
                } */}
                  <Grid item xs={12}>
                    <FormControlLabel
                      control={
                        <Checkbox
                          checked={this.state.downloadType.product}
                          value='product'
                          color="primary"
                          onChange={this.onDownloadType.bind(this, 'product')}
                        />
                      }
                      label='Product Image'
                    />

                    <FormControlLabel
                      control={
                        <Checkbox
                          checked={this.state.downloadType.upc}
                          value='upc'
                          color="primary"
                          onChange={this.onDownloadType.bind(this, 'upc')}
                        />
                      }
                      label='UPC'
                    />
                  </Grid>
                </Grid>
              </CardContent>
            </Card>
            <Card>
              <CardContent>
                <Button disabled={this.state.downloadLoading} variant="contained" color="primary" onClick={this.onDownloadZIP}>Download ZIP</Button>
              </CardContent>
              {
                this.state.downloadDone &&
                <div>
                  <span>Total: {Object.keys(this.state.searchItem).length}</span>
                  <span> Success: {Object.keys(this.state.searchItem).filter(key => {return !this.state.searchItem[key].status} ).length}</span>
                  <span> Fail: {Object.keys(this.state.searchItem).filter(key => {return this.state.searchItem[key].status} ).length}</span>
                </div>
              }
              {
                this.state.downloadDone &&
                <Table >
                  <TableHead>
                    <TableRow>
                      <TableCell>folder Name</TableCell>
                      <TableCell>Status</TableCell>
                      <TableCell align="right">Itmes</TableCell>
                      <TableCell align="right">Total</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {Object.keys(this.state.searchItem).map(key => {
                      return (
                        <TableRow key={key}>
                          <TableCell component="th" scope="row">
                            {key}
                          </TableCell>
                          <TableCell component="th" scope="row">
                            { this.state.searchItem[key].status || 'success'}
                          </TableCell>
                          <TableCell align="right">
                            {
                              this.state.searchItem[key].data.map((item, i) =>{
                                return <div key={i}>{item.title}</div>
                              })
                            }
                            </TableCell>
                          <TableCell align="right">{this.state.searchItem[key].data.length}</TableCell>
                        </TableRow>
                      );
                    })
                  }
                  </TableBody>
                </Table>
              }
            </Card>
          </Box>
        }
      </div>
    );
  }
}

export default App;
